<template>
  <div id="sale">
    <v-container>
      <h5 class="my-4"><span class="cblu bbred">All Sales</span></h5>
      <v-row>
        <v-col cols="12" md="3">
          <div class="mobile">
            <div class="d-flex justify-content-between">
              <v-btn small @click="price">Price</v-btn>
              <v-btn small @click="deals">Jobs Near By</v-btn>
            </div>
          </div>
          <div class="catside shadow desktop">
            <div class="h5 sec1">Price</div>
            <div class="range-slider sec1 bg-white p-2">
              <span class="d-flex">
                <input :value="range[0]" class="me-2 form-control text-center"  @change="$set(range, 0, $event)">
                <input :value="range[1]" class="me-2 form-control text-center"  @change="$set(range, 1, $event)">
              </span>
              <v-range-slider v-model="range" :max="max" :min="min" class="align-center"></v-range-slider>
            </div>
            <div class="h5 sec1">Jobs Near By</div>
            <v-range-slider v-model="range1" :max="max1" :min="min1" class="align-center"></v-range-slider>
                <div class="text-center small">{{ range1[0] }} Km - {{ range1[1] }} Km</div>
            <v-btn color="success" class="my-2">Go</v-btn>
          </div>
          <v-dialog v-model="dialog1" persistent>
            <v-card>
              <span class="d-flex justify-content-between">
                  <v-card-title>Price</v-card-title>
                  <v-icon @click="dialog1 = false" class="me-3">mdi-close</v-icon>
              </span>
              <hr class="m-0">
              <v-card-text>
                <v-range-slider
                    v-model="range" :max="max" :min="min" class="align-center"></v-range-slider>
                <span class="d-flex">
                      <input :value="range[0]" class="me-2 form-control text-center"  @change="$set(range, 0, $event)">
                      <input :value="range[1]" class="me-2 form-control text-center"  @change="$set(range, 1, $event)">
                    </span>
              </v-card-text>
              <div class="text-center">
                <b-button class="mb-3" variant="success" type="submit">Go</b-button>
              </div>
            </v-card>
          </v-dialog>
          <v-dialog v-model="dialog2" persistent>
            <v-card>
              <span class="d-flex justify-content-between">
                  <v-card-title>Deals Radar</v-card-title>
                  <v-icon @click="dialog2 = false" class="me-3">mdi-close</v-icon>
              </span>
              <hr class="m-0">
              <v-card-text>
                <v-range-slider v-model="range1" :max="max1" :min="min1" class="align-center"></v-range-slider>
                <div class="text-center h6 link-dark">{{ range1[0] }} Km - {{ range1[1] }} Km</div>
              </v-card-text>
              <div class="text-center">
                <b-button class="mb-3" variant="success" type="submit">Go</b-button>
              </div>
            </v-card>
          </v-dialog>
        </v-col>
        <v-col cols="12" md="9">
          <div class="mobile" v-if="rlists.length > 1">
            <v-card v-for="(sale,i) in rlists" :key="i" :perPage="perPage" class="item mitem my-2 mb-7">
              <div class="small d-flex">
                <router-link :to="{name:'saledetail',params:{id:sale.id}}">
                  <div class="mimg"><v-img v-if="sale.sale_imageurl!=''" :src="url+sale.sale_imageurl" cover class="rounded-3" height="110"></v-img>
                    <v-img v-if="sale.sale_imageurl==''" :src="('https://dummyimage.com/400x350/fd0606/fff&text=Sale')" cover class="rounded-3" height="110"></v-img>
                  </div>
                </router-link>
                <div class="mdetails">
                  <router-link :to="{name:'saledetail',params:{id:sale.id}}" class="mtitle text-decoration-none link-dark h4">
                    {{ sale.sale_title }}
                  </router-link>
                  <div>Add to Wishlist<v-icon small>mdi-heart-outline</v-icon></div>
                  <div class="">Sale
                    <del class=" ms-1 h6">₹{{ sale.normal_price }}</del>
                    <span class=" ms-1 h4 cred">₹{{ sale.sale_price }}</span>
                  </div>
                  <div>Validity: {{ sale.saledate_from }} - {{ sale.saledate_to }} </div>
                  <div class="address">
                    <v-icon small>mdi-map-marker</v-icon>
                    {{ sale.city }}, {{ sale.state }}
                  </div>
                  <router-link :to="{name:'saledetail',params:{id:sale.id}}" class="cred">Read More</router-link>
                </div>
                <div class="mcontact">
                  <a :href="('https://wa.me/'+sale.mobile_phone+'?text=rf')">
                    <v-btn fab small color="success" class="mb-5 btnsm"><v-icon>mdi-whatsapp</v-icon></v-btn>
                  </a>
                  <a :href="('tel:'+ sale.mobile_phone)">
                    <v-btn fab small color="primary" class="btnsm"><v-icon>mdi-phone</v-icon></v-btn>
                  </a>
                </div>
              </div>
            </v-card>
          </div>
          <div class="desktop" v-if="rlists.length > 1">
            <v-card class="mb-7" v-for="(sale,i) in rlists" :key="i" :perPage="perPage">
              <v-row>
                <v-col cols="12" md="4" class="py-0">
                  <router-link :to="{name:'saledetail',params:{id:sale.sale_id}}">
                    <v-img v-if="sale.sale_imageurl!=''" :src="url+sale.sale_imageurl" cover height="200"></v-img>
                    <v-img v-if="sale.sale_imageurl==''" :src="('https://dummyimage.com/400x350/fd0606/fff&text=Sale')" cover height="200"></v-img>
                  </router-link>
                </v-col>
                <v-col cols="12" md="8" class="py-0">
                  <v-card-text>
                    <v-row>
                      <v-col cols="12" md="8">
                        <router-link :to="{name:'saledetail',params:{id:sale.sale_id}}" class="text-decoration-none link-dark h4">
                          {{ sale.sale_title }}
                        </router-link>
                        <div>Add to Wishlist<v-icon small>mdi-heart-outline</v-icon></div>
                        <div class="">Sale
                          <del class=" ms-1 h6">₹{{ sale.normal_price }}</del>
                          <span class=" ms-1 h4 cred">₹{{ sale.sale_price }}</span>
                        </div>
                        <div>Validity: {{ sale.saledate_from }} - {{ sale.saledate_to }} </div>
                        <div class="address">
                          <v-icon small>mdi-map-marker</v-icon>
                         {{ sale.city }}, {{ sale.state }}
                          <router-link :to="{name:'saledetail',params:{id:sale.sale_id}}" class="cred">Read More</router-link>
                        </div>
                      </v-col>
                      <v-col cols="12" md="4">
                        <a :href="('tel:'+ sale.mobile_phone)">
                          <v-btn small color="primary" class="me-1 mb-2">
                            <v-icon small>mdi-phone</v-icon>Call Now</v-btn>
                        </a>
                        <a :href="('https://wa.me/'+sale.mobile_phone+'?text=rf')">
                          <v-btn small color="success" class="mb-2">
                            <v-icon small>mdi-whatsapp</v-icon>WhatsApp</v-btn>
                        </a>
                      </v-col>
                    </v-row>
                  </v-card-text>
                </v-col>
              </v-row>
            </v-card>
          </div>
          <v-pagination v-if="rlists.length > 1" class="pagination pt-3" v-model="page" :length="Math.ceil(allSales.length/perPage)" circle></v-pagination>
        </v-col>
      </v-row>
    </v-container>

  </div>
</template>

<script>
import axios from 'axios';
import { EventBus } from "@/event-bus";
export default {
  name: "SalesAll",
  data() {
    return{
      url:'https://bizlx.itechvision.in',
      page:1,
      perPage:10,
      allSales:[],
      dialog1: false,
      dialog2: false,
      dialog3: false,
      catvalue:null,
      selected: null,
      min: 0,
      max: 120000,
      range: [],
      range1: [0, 100],
      min1: 0,
      max1: 100,
      sale_price: [],
    }
  },
  methods:{
    price() {
      this.dialog1 = true;
    },
    deals() {
      this.dialog2 = true;
    },
    category() {
      this.dialog3 = true;
    },
  },
  mounted(){
      EventBus.$on('event_bus_emitted', (data) =>{
          this.allSales = data.allSales;
          console.warn(this.allSales);
      });
      const config = {
          headers: { 'content-type': 'multipart/form-data' }
      }
      this.searchedCatData = JSON.parse(localStorage.getItem("catSearchedValues"));
      axios.post("search/category", this.searchedCatData, config)
          .then((resp) => {
            if(resp.data.dealsType=="sales"){ // deals page
                this.allSales = resp.data.allSales;

                for(var k = 0; k < this.allSales.length; k++) { // salary
                  this.sale_price.push(this.allSales[k].sale_price);
                }
                this.sale_price = [...new Set(this.sale_price.sort())];
                this.range = [this.sale_price.slice(0)[0], this.sale_price.slice(-1)[0]]; // min & max price set
            
              }else{
              this.$router.push({name: 'home'});
            }
          },
      );
  },
  computed:{
    rlists(){
      return this.allSales.slice((this.page - 1)*this.perPage, this.page*this.perPage)
    }
  },
}
</script>

<style scoped>
.item.mitem {
  box-shadow: 0 6px 10px -6px rgba(0,0,0,0.5);
  background: #f1f2f380;
  padding: 7px;
}
.mimg {
  width: 110px;
}
.mtitle {
  font-weight: 600;
  font-size: 12.25px;
}
.mdetails {
  width: 100%;
  padding: 0px 0 0 7px;
}
.mcontact {
  width: 42px;
}
.fs11{font-size: 11px;}
.catside{
  background: #e5e5e5;
  padding: 10px;
}
.sec1{
  background: #f2f2f2;
  padding: 5px 15px;
  margin-bottom: 20px;
}
</style>